var tagcounter=0
var tagSelected=""
var hintc = 0

document.onclick = function(e){
  if(e.target.id!=="tagMenu" && e.target.id!=="tagMenuButton" && e.target.id!=="tagInput"){
    document.getElementById("tagMenu").style.display = "none";
  }
}




export default {
  
  name: 'App',
  data(){
    return{
      image: { url: ""},
      cat_api_key:"317ea028-971e-436c-9a4b-1602401a458a",
      catFact:"",
      catFactTranslate:"",
      imgPath:"",
      gifPath:"",
      talkingCatPhrase:"",
      talkingCatImg:thinkingcat,
      generuj: generujButton,
      tagSelected:"",
      tagInputValue:"",
      hint:"<-- klikaj na obrazki aby przewijać"
    }
  },
  mounted(){
        fetch("https://catfact.ninja/fact")
        .then(response=>{
          return response.json();
        })
        .then(data=>{
          console.log(data)
          this.catFact = data.fact
          this.catFactTranslate = this.catFact

          while(this.catFactTranslate.indexOf(" ")>0){
            this.catFactTranslate=this.catFactTranslate.replace(" ","%20")
        }

        this.catFactTranslate = "https://translate.google.pl/?hl=pl&sl=en&tl=pl&text="+this.catFactTranslate

        })
        this.getRandomImgCat()
        this.getRandomGifCat()
  },
  methods:{
    getRandomImgCat(){
      fetch("https://api.thecatapi.com/v1/images/search?mime_types=jpg,png")
      .then(res=>{
        return res.json();
      })
      .then(data=>{
        this.imgPath = data[0].url
      })

    },
    getRandomGifCat(){
      hintc++
      if(hintc==2){
        document.getElementById("hint").style.display = "none"
      }
      fetch("https://api.thecatapi.com/v1/images/search?mime_types=gif")
      .then(res=>{
        return res.json();
      })
      .then(data=>{
        this.gifPath = data[0].url
      })

    },
    getTalkingCat(){
      hintc++
      if(hintc==2){
        document.getElementById("hint").style.transition = "height 1s"
      }
      if(document.getElementById("talkingCatInput").value==''){
        alert("musisz coś wpisać")
        return
      }
      fetch("https://cataas.com/cat/"+tagSelected+"says/"+document.getElementById("talkingCatInput").value+"?json=true")
      .then(res=>{
        return res.json()
      })
      .then(data=>{
        this.talkingCatImg="https://cataas.com"+data.url
      })
      .catch(error => {
        alert(error+"prawdopodobnie nie ma kotka dla takiego taga, wybierz inny tag");
      })
    },
    newLine(){
      document.getElementById("talkingCatInput").value+="%0D%0A"
    },
    showTagMenu(){
      document.getElementById("tagMenu").style.display = "block"
      document.getElementById("tagMenu").style.top = (document.getElementById("tagInput").getBoundingClientRect().y+document.getElementById("tagInput").clientHeight)+3+"px";
      document.getElementById("tagMenu").style.left = document.getElementById("tagInput").getBoundingClientRect().x+"px";
      document.getElementById("tagMenu").style.width = document.getElementById("tagInput").offsetWidth+"px"
      //tworzenie divow z tagami
      while(tagcounter!=tagi.length){
        var tagdiv = document.createElement("div")
        tagdiv.className = "tagMenuElement"
        tagdiv.textContent = tagi[tagcounter]
        tagdiv.onclick = function(){
          document.getElementById("tagInput").value = this.textContent
          tagSelected=this.textContent+"/"
          document.getElementById("tagMenu").style.display = "none"
        }
        document.getElementById("tagMenu").appendChild(tagdiv)
        tagcounter++
      }
    },
    searchTag(){
      let tagelement = document.getElementsByClassName("tagMenuElement")
      for(let i=0;i!=550;i++){
        if(tagelement[i].textContent.toLowerCase().includes(this.tagInputValue.toLowerCase())==false){
          tagelement[i].style.display = "none"
        }
        else{
          tagelement[i].style.display = "block"
        }
      }

      }
    }
  }

</script>

